/* Image Slider Wrapper */
.image-slider-wrapper {
  width: 100%;
  height: 400px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  background-color: #f8f9fa;
}

/* Slider Slide */
.slider-slide {
  outline: none;
}

.slider-image-container {
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f8f9fa;
}

.slider-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Slick Carousel Customization */
.image-slider-wrapper .slick-dots {
  bottom: 20px;
  z-index: 10;
}

.image-slider-wrapper .slick-dots li button:before {
  color: white;
  font-size: 12px;
  opacity: 0.5;
}

.image-slider-wrapper .slick-dots li.slick-active button:before {
  color: #ff6b35;
  opacity: 1;
}

.image-slider-wrapper .slick-dots li button:before {
  content: '●';
}

/* Стрелки навигации */
.image-slider-wrapper .slick-prev,
.image-slider-wrapper .slick-next {
  z-index: 10;
  width: 50px;
  height: 50px;
}

.image-slider-wrapper .slick-prev {
  left: 15px;
}

.image-slider-wrapper .slick-next {
  right: 15px;
}

.image-slider-wrapper .slick-prev:before,
.image-slider-wrapper .slick-next:before {
  font-size: 40px;
  color: rgba(255, 255, 255, 0.9);
  background-color: rgba(30, 60, 114, 0.8);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 50px;
  transition: all 0.3s ease;
}

.image-slider-wrapper .slick-prev:hover:before,
.image-slider-wrapper .slick-next:hover:before {
  background-color: rgba(30, 60, 114, 1);
  transform: scale(1.1);
}

/* Адаптивность */
@media (max-width: 768px) {
  .image-slider-wrapper {
    height: 300px;
  }

  .slider-image-container {
    height: 300px;
  }

  .image-slider-wrapper .slick-prev,
  .image-slider-wrapper .slick-next {
    width: 40px;
    height: 40px;
  }

  .image-slider-wrapper .slick-prev:before,
  .image-slider-wrapper .slick-next:before {
    font-size: 30px;
    width: 40px;
    height: 40px;
    line-height: 40px;
  }

  .image-slider-wrapper .slick-prev {
    left: 10px;
  }

  .image-slider-wrapper .slick-next {
    right: 10px;
  }

  .image-slider-wrapper .slick-dots {
    bottom: 15px;
  }
}

/* Переопределение стилей slick-theme для нашего проекта */
.image-slider-wrapper .slick-dots li {
  margin: 0 5px;
}

.image-slider-wrapper .slick-dots li button {
  width: 12px;
  height: 12px;
  padding: 0;
}

.image-slider-wrapper .slick-dots li.slick-active button:before {
  color: #ff6b35;
  opacity: 1;
}
